package com.ragnorak.cmparticle.screens

import androidx.compose.animation.animateColorAsState
import androidx.compose.animation.core.tween
import androidx.compose.foundation.BorderStroke
import androidx.compose.foundation.Image
import androidx.compose.foundation.background
import androidx.compose.foundation.clickable
import androidx.compose.foundation.layout.*
import androidx.compose.foundation.lazy.LazyColumn
import androidx.compose.foundation.lazy.items
import androidx.compose.material.*
import androidx.compose.material.icons.Icons
import androidx.compose.material.icons.automirrored.filled.ArrowBack
import androidx.compose.material.icons.automirrored.filled.ArrowForward
import androidx.compose.runtime.*
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.Brush
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.layout.ContentScale
import androidx.compose.ui.platform.LocalUriHandler
import androidx.compose.ui.text.font.FontStyle
import androidx.compose.ui.text.font.FontWeight
import androidx.compose.ui.unit.dp
import androidx.compose.ui.unit.sp
import cafe.adriel.voyager.core.screen.Screen
import cafe.adriel.voyager.navigator.LocalNavigator
import cafe.adriel.voyager.navigator.currentOrThrow
import com.ragnorak.cmparticle.service.ProjectsProvider
import composempwebarticle.composeapp.generated.resources.Res
import composempwebarticle.composeapp.generated.resources.ic_git
import composempwebarticle.composeapp.generated.resources.ic_theme
import kotlinx.coroutines.delay
import org.jetbrains.compose.resources.painterResource

data class ProjectScreen(val device: String) : Screen {
    @Composable
    override fun Content() {
        val isDesktop by remember { mutableStateOf(device == "Desktop") }
        val navigator = LocalNavigator.currentOrThrow
        val itemsProject = ProjectsProvider.projects
        val uriHandler = LocalUriHandler.current
        var changeTextColor by remember { mutableStateOf(false) }
        val colorMain = if (changeTextColor) Color.Black else Color.LightGray

        Column(
            modifier = Modifier.fillMaxSize()
                .background(
                    if (changeTextColor) Brush.horizontalGradient(
                        listOf(
                            Color.LightGray,
                            Color.DarkGray
                        )
                    ) else Brush.horizontalGradient(listOf(Color.DarkGray, Color.Black))
                ),
            horizontalAlignment = Alignment.CenterHorizontally
        ) {
            var red by remember { mutableStateOf(false) }

            val animatedColor by animateColorAsState(
                targetValue = if (red) Color.Red else colorMain,
                animationSpec = tween(durationMillis = 1000)
            )

            LaunchedEffect(Unit) {
                while (true) {
                    delay(1000)
                    red = !red
                }
            }

            Box(modifier = Modifier.fillMaxWidth().height(48.dp).background(Color.DarkGray)) {

                Icon(
                    Icons.AutoMirrored.Filled.ArrowBack,
                    null,
                    tint = Color.White,
                    modifier = Modifier.align(Alignment.CenterStart).padding(start = 16.dp)
                        .clickable { navigator.pop() }
                )
                Text(
                    "My Projects", fontStyle = FontStyle.Italic,
                    modifier = Modifier.align(Alignment.Center),
                    fontSize = 24.sp,
                    fontWeight = FontWeight.ExtraBold,
                    color = animatedColor
                )
                Icon(
                    painterResource(Res.drawable.ic_theme),
                    null,
                    tint = if (changeTextColor) Color.Black else Color.White,
                    modifier = Modifier.align(Alignment.CenterEnd).padding(end = 16.dp).size(60.dp)
                        .clickable { changeTextColor = !changeTextColor })
            }
            Divider(
                modifier = Modifier
                    .fillMaxWidth()
                    .height(12.dp),
                color = animatedColor
            )

            LazyColumn(
                horizontalAlignment = Alignment.CenterHorizontally,
                verticalArrangement = Arrangement.Center,
                userScrollEnabled = true
            ) {
                items(itemsProject) { project ->
                    var currentPhotoIndex by remember { mutableStateOf(0) }

                    Column(modifier = Modifier.fillMaxWidth(), horizontalAlignment = Alignment.CenterHorizontally) {
                        // Nombre del proyecto
                        Text(
                            modifier = Modifier.padding(top = 8.dp, bottom = 2.dp),
                            text = project.name,
                            fontStyle = FontStyle.Italic,
                            fontSize = 30.sp,
                            fontWeight = FontWeight.ExtraBold,
                            color = if (changeTextColor) Color.Unspecified else Color.White
                        )
                        Spacer(modifier = Modifier.size(4.dp))

                        // Contenedor de la imagen con flechas
                        Box(contentAlignment = Alignment.Center, modifier = Modifier.fillMaxWidth()) {
                            // Flecha izquierda
                            IconButton(
                                onClick = {
                                    if (currentPhotoIndex > 0) currentPhotoIndex--
                                },
                                modifier = Modifier.align(Alignment.CenterStart).padding(start = 8.dp)
                            ) {
                                Icon(
                                    imageVector = Icons.AutoMirrored.Filled.ArrowBack,
                                    contentDescription = "Previous photo",
                                    tint = if (changeTextColor) Color.Black else Color.White
                                )
                            }

                            // Imagen actual
                            Image(
                                painter = painterResource(project.photo[currentPhotoIndex]),
                                contentDescription = null,
                                modifier = Modifier
                                    .height(400.dp)
                                    .width(200.dp)
                                    .padding(top = 2.dp),
                                contentScale = ContentScale.Crop
                            )

                            // Flecha derecha
                            IconButton(
                                onClick = {
                                    if (currentPhotoIndex < project.photo.size - 1) currentPhotoIndex++
                                },
                                modifier = Modifier.align(Alignment.CenterEnd).padding(end = 8.dp)
                            ) {
                                Icon(
                                    imageVector = Icons.AutoMirrored.Filled.ArrowForward,
                                    contentDescription = "Next photo",
                                    tint = if (changeTextColor) Color.Black else Color.White
                                )
                            }
                        }
                        Spacer(modifier = Modifier.size(4.dp))

                        // Descripcin del proyecto
                        Text(
                            modifier = Modifier.padding(vertical = 4.dp).padding(horizontal = 8.dp),
                            text = project.description,
                            fontSize = if (isDesktop) 28.sp else 20.sp,
                            fontWeight = FontWeight.Bold,
                            color = if (changeTextColor) Color.Unspecified else Color.White
                        )
                        Spacer(modifier = Modifier.size(4.dp))

                        // Botn para ir al proyecto
                        Button(
                            modifier = Modifier.height(50.dp).width(200.dp).padding(top = 6.dp, bottom = 8.dp),
                            onClick = { uriHandler.openUri(project.url) },
                            colors = ButtonDefaults.buttonColors(
                                contentColor = Color.Black,
                                backgroundColor = Color.DarkGray
                            ),
                            elevation = ButtonDefaults.elevation(24.dp),
                            border = BorderStroke(1.dp, Color.White)
                        ) {
                            Row(verticalAlignment = Alignment.CenterVertically) {
                                Icon(
                                    painter = painterResource(Res.drawable.ic_git),
                                    contentDescription = null,
                                    modifier = Modifier.size(24.dp),
                                    tint = Color.White
                                )
                                Spacer(Modifier.width(8.dp))
                                Text(
                                    "Go to project",
                                    fontStyle = FontStyle.Italic,
                                    fontSize = 16.sp,
                                    fontWeight = FontWeight.ExtraBold,
                                    color = Color.White
                                )
                            }
                        }

                        // Divider
                        Divider(
                            modifier = Modifier
                                .fillMaxWidth()
                                .size(16.dp),
                            color = animatedColor
                        )
                    }
                }
            }

        }

    }
}