@Composable
fun App() {
    Navigator(screen = MainScreen()) { navigator ->
        SlideTransition(navigator)
    }
}

class MainScreen : Screen {
    private val device = getDeviceType()

    @Composable
    override fun Content() {
        WebProfile(device)
    }

}

@Composable
fun WebProfile(device: String) {
    MaterialTheme {
        val navigator = LocalNavigator.currentOrThrow
        val uriHandler = LocalUriHandler.current
        val horizontalScroll = rememberScrollState()
        val project = "https://github.com/Dangerous875?tab=repositories&q=&type=public&language=&sort="
        val git = "https://github.com/Dangerous875"
        val link = "https://www.linkedin.com/in/cristian-barzabal-84017b2b1/"
        val items = LinkProvider.items
        var changeTextColor by remember { mutableStateOf(false) }
        val isDesktop by remember { mutableStateOf(device == "Desktop") }
        LaunchedEffect(Unit) {
            val percentageToScroll = 0.7f
            val scrollPosition = (horizontalScroll.maxValue * percentageToScroll).toInt()
            horizontalScroll.scrollTo(scrollPosition)
        }
        Column(
            modifier = Modifier.fillMaxSize()
                .background(
                    if (changeTextColor) Brush.horizontalGradient(
                        listOf(
                            Color.LightGray,
                            Color.DarkGray
                        )
                    ) else Brush.horizontalGradient(listOf(Color.DarkGray, Color.Black))
                ),
            horizontalAlignment = Alignment.CenterHorizontally
        ) {
            Box(modifier = Modifier.fillMaxWidth().height(48.dp).background(Color.DarkGray)) {
                Image(
                    painterResource(Res.drawable.iv_kotliniv),
                    null,
                    modifier = Modifier.align(Alignment.CenterStart).size(80.dp)
                        .padding(start = if (isDesktop) 16.dp else 8.dp)
                )
                Text(
                    "Te damos la bienvenida",
                    modifier = Modifier.align(Alignment.Center),
                    fontSize = if (isDesktop) 24.sp else 16.sp,
                    fontWeight = FontWeight.ExtraBold,
                    color = Color.White,
                    fontStyle = FontStyle.Italic,
                )
                Icon(
                    painterResource(Res.drawable.ic_theme),
                    null,
                    tint = if (changeTextColor) Color.Black else Color.White,
                    modifier = Modifier.align(Alignment.CenterEnd).padding(end = 16.dp).size(60.dp)
                        .clickable { changeTextColor = !changeTextColor }
                )

            }
            Spacer(Modifier.size(20.dp))
            Row(
                modifier = Modifier.fillMaxWidth().weight(1f).padding(top = 8.dp).horizontalScroll(horizontalScroll),
                horizontalArrangement = Arrangement.SpaceAround,
                verticalAlignment = Alignment.CenterVertically
            ) {
                if (isDesktop) {
                    SetIcon(
                        Res.drawable.ic_apple,
                        "Apple",
                        Color.DarkGray,
                        Color.LightGray,
                        changeTextColor
                    )
                    SetIcon(
                        Res.drawable.ic_androidb,
                        "Android",
                        Color.DarkGray,
                        Color.LightGray,
                        changeTextColor
                    )
                    SetProfile(
                        Res.drawable.iv_profile,
                        changeTextColor
                    )
                    SetIcon(
                        Res.drawable.ic_web,
                        "Web",
                        Color.LightGray,
                        Color.DarkGray,
                        changeTextColor
                    )
                    SetIcon(
                        Res.drawable.ic_desktop,
                        "Desktop",
                        Color.LightGray,
                        Color.DarkGray,
                        changeTextColor
                    )
                } else {
                    SetProfile(
                        Res.drawable.iv_profile,
                        changeTextColor
                    )
                }
            }
            GenerateButton(link, uriHandler, items[0], navigator, device)
            GenerateButton(git, uriHandler, items[1], navigator, device)
            GenerateButton(project, uriHandler, items[2], navigator, device)
            Column(
                modifier = Modifier.weight(1f),
                verticalArrangement = Arrangement.SpaceAround,
                horizontalAlignment = Alignment.CenterHorizontally
            ) {

                var isRed by remember { mutableStateOf(false) }
                val colorMain = if (changeTextColor) Color.Black else Color.White
                val animatedColor by animateColorAsState(
                    targetValue = if (isRed) {
                        Colors.kotlin
                    } else {
                        colorMain
                    },
                    animationSpec = androidx.compose.animation.core.tween(durationMillis = 1000)
                )

                LaunchedEffect(Unit) {
                    while (true) {
                        delay(1000)
                        isRed = !isRed
                    }
                }

                Text(
                    text = "Website made 100% in Kotlin", fontSize = 24.sp,
                    fontWeight = FontWeight.Bold, color = animatedColor
                )
                Spacer(modifier = Modifier.size(4.dp))
                ContactButton(changeTextColor)
            }


        }


    }
}

@Composable
fun GenerateButton(url: String, uriHandler: UriHandler, linkItem: LinkItem, navigator: Navigator, device: String) {
    val interactionSource = remember { MutableInteractionSource() }
    val isHovered by interactionSource.collectIsHoveredAsState()

    val backgroundColor by animateColorAsState(if (isHovered) linkItem.backgroundColorHoverColor else linkItem.backgroundColor)

    val textColor by animateColorAsState(if (isHovered) linkItem.textHoverColor else linkItem.textColor)

    Button(
        onClick = { if (linkItem.name == "Projects") navigator.push(ProjectScreen(device)) else uriHandler.openUri(url) },
        modifier = Modifier.fillMaxWidth().padding(vertical = 6.dp, horizontal = 16.dp)
            .height(60.dp),
        interactionSource = interactionSource,
        colors = ButtonDefaults.buttonColors(backgroundColor = backgroundColor)
    ) {
        Row(verticalAlignment = Alignment.CenterVertically) {
            AnimatedVisibility(visible = isHovered) {
                Image(painterResource(Res.drawable.ic_fire2), contentDescription = null)
                Spacer(Modifier.width(48.dp))
            }
            Icon(
                painterResource(linkItem.icon),
                contentDescription = null,
                modifier = Modifier.size(24.dp).fillMaxSize(1.0F),
                tint = textColor
            )
            Spacer(Modifier.width(8.dp))
            Text(
                text = linkItem.name,
                fontWeight = FontWeight.ExtraBold,
                fontSize = 20.sp,
                color = textColor
            )
            AnimatedVisibility(visible = isHovered) {
                Image(painterResource(Res.drawable.ic_fire2), contentDescription = null)
                Spacer(Modifier.width(48.dp))
            }
        }
    }
}

@Composable
fun SetProfile(ivProfile: DrawableResource, darkTheme: Boolean) {
    Box(contentAlignment = Alignment.Center) {
        Column(horizontalAlignment = Alignment.CenterHorizontally) {
            Image(
                painter = painterResource(ivProfile),
                contentDescription = null,
                modifier = Modifier.weight(1f).clip(CircleShape),
                contentScale = ContentScale.Fit
            )
            Text(
                text = "Barzabal Cristian Profile",
                fontSize = 20.sp,
                fontWeight = FontWeight.ExtraBold,
                color = if (darkTheme) Color.Unspecified else Color.White
            )
            Text(
                text = "Mobile developer && IT technician Freelancer",
                fontWeight = FontWeight.ExtraBold,
                color = if (darkTheme) Color.Unspecified else Color.White
            )
            Text(
                text = "KotlinMultiplatformProfile",
                fontWeight = FontWeight.ExtraBold,
                color = if (darkTheme) Color.Unspecified else Color.White
            )
        }
    }
}

@Composable
fun SetIcon(
    iconPainter: DrawableResource,
    name: String,
    colorOriginal: Color,
    colorClick: Color,
    darkTheme: Boolean
) {
    Box(
        contentAlignment = Alignment.Center
    ) {
        Column(horizontalAlignment = Alignment.CenterHorizontally) {
            Icon(
                modifier = Modifier.weight(1f),
                painter = painterResource(iconPainter),
                contentDescription = null,
                tint = if (darkTheme) colorOriginal else colorClick
            )
            Spacer(modifier = Modifier.size(2.dp))
            Text(
                text = name, fontSize = 16.sp,
                color = if (darkTheme) colorOriginal else colorClick,
                fontWeight = FontWeight.Bold
            )
        }
    }
}


@Composable
fun ContactButton(darkTheme: Boolean) {
    var showContent by remember { mutableStateOf(false) }
    Column(
        horizontalAlignment = Alignment.CenterHorizontally,
        verticalArrangement = Arrangement.SpaceAround
    ) {
        Button(
            modifier = Modifier.height(60.dp).width(300.dp),
            onClick = { showContent = !showContent },
            colors = ButtonDefaults.buttonColors(
                contentColor = Color.Black,
                backgroundColor = Color.DarkGray
            ),
            elevation = ButtonDefaults.elevation(24.dp),
            border = BorderStroke(1.dp, Color.White)

        ) {
            Text(text = "Contactar ahora", fontSize = 24.sp, fontWeight = FontWeight.ExtraBold, color = Color.White)
        }
        AnimatedVisibility(showContent) {
            Column(horizontalAlignment = Alignment.CenterHorizontally, verticalArrangement = Arrangement.Center) {
                Spacer(modifier = Modifier.height(16.dp))
                Row(verticalAlignment = Alignment.CenterVertically) {

                    Icon(
                        modifier = Modifier.size(24.dp),
                        painter = painterResource(Res.drawable.ic_what),
                        contentDescription = null, tint = Color.Green
                    )
                    Spacer(modifier = Modifier.width(8.dp))
                    Text(
                        text = "+5491168298219",
                        fontWeight = FontWeight.ExtraBold,
                        color = if (darkTheme) Color.Unspecified else Color.White
                    )
                }
                Spacer(modifier = Modifier.height(8.dp))
                Row(verticalAlignment = Alignment.CenterVertically) {

                    Image(
                        modifier = Modifier.size(34.dp),
                        painter = painterResource(Res.drawable.ic_gmail),
                        contentDescription = null
                    )
                    Spacer(modifier = Modifier.width(8.dp))
                    Text(
                        text = "crbarzabal@gmail.com",
                        fontWeight = FontWeight.ExtraBold,
                        color = if (darkTheme) Color.Unspecified else Color.White
                    )
                }
            }
        }
    }
}